@import '../../styles/main.scss';
.section{
  position: relative;
}
.main_container {
  
  margin-left: auto;
  margin-right: auto;
  // width: 100%; 
  @media screen and (min-width: 768px) {
    width: 768px;
  }
  @media screen and (min-width: 1200px) {
    width: 1440px;
  }
}

.title{
  margin-bottom: 28px;
  margin-top: 160px;
display: block;
width: 430px;
  color: var(--main-text-color);
  font-feature-settings: 'liga' off;
  font-family: Manrope;
  font-size: 38px;
  font-style: normal;
  font-weight: 800;
  line-height: 104%;
  letter-spacing: -0.36px;

  @media screen and (min-width: 768px) {
    font-size: 40px;
    line-height: 44px;
    letter-spacing: -0.02em;
  }
}

.btn_add{
  display:flex;
   width: 200px;
   height: 54px;
   margin-top: 40px;
}

.btn_other{
  display:flex;
  width: 200px;
  height: 54px;
  margin-top: 80px;
  margin-right: auto;
  margin-left: auto;
}

.main_foto{
  position: absolute;
  top: -245px;
  right: -550px;
  z-index: -2;
//  margin-left: 200px;
 @media screen and (min-width: 768px) {
 
 right: -490px;
}
@media screen and (min-width: 1200px) {
  right: 0;
}
}

.wrap_main {
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 80px;
  margin-top: 316px;

  @media screen and (min-width: 768px) {
    padding-left: 32px;
    padding-right: 32px;
    margin-bottom: 140px;
  }
  // @media screen and (min-width: 1200px) {
  //   padding-left: 100px;
  //   padding-right: 100px;
  // }
}




